'use strict';
var React = require('react');
var ReactDOM = require('react-dom');
var OtherSay = require('./OtherSay.jsx');
var InputBlock = require('./InputBlock.jsx');
var moment = require('moment');
var ISAY = require('./ISay.jsx');

var config = {
    server : 'http://www.itshowtime.idv.tw/chatroom',
    name : 'TomasLin',
    logo : 'http://etech.gac.ntnu.edu.tw/responsiva/cs/images/art_Sun_Flower.jpg'
};

var Main = React.createClass({
    getInitialState : function(){
        return {
            messages : []
        };
    },
    componentDidMount: function(){
        var _this = this;
        setInterval(function(){
            request('/rooms/list/v1/', {}).done(function(data){
                console.log(data);
            });
        }, 15000)
    },
    addmessage : function(content){
        var messages = this.state.messages;
        messages.push({
            name : config.name,
            content : content,
            logo : config.logo,
            created_time : moment().format('YYYY-MM-DD A hh:mm:ss')
        });
        this.setState({messages : messages});
    },
    render : function(){
        return (
            <div className='panel panel-default'>
                <div className='panel-heading'>聊天室</div>
                <div className='panel-body'>
                    {this.state.messages.map(function(message){
                        return (<ISAY
                                    message={message}/>);
                    })}
                </div>
                <div className='panel-footer'>
                    <InputBlock
                        addmessage={this.addmessage}/>
                </div>
            </div>
        )
    }
});

ReactDOM.render(
    <Main />,
      document.getElementById('container'));

//使用ajax 發送request
function request(route, data){
    var url = config.server + '/' + route;
    return $.ajax({
        url : url,
        method : 'POST',
        contentType : 'application/json',
        data : data
    });
}



